.app-wrapper {
  display: grid;
  grid-template-areas: "header header" "menu content";
  grid-template-rows: 50px 1fr;
  grid-template-columns: 150px 1fr;
  height: 100vh;
}

.content-wrapper {
  grid-area: content;
  background-color: rgb(223, 223, 255);
}

.pagination .active {
  font-weight: bold;
}