.user_container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid grey;
    padding: 10px;
    margin: 5px;
}

.avatar_container img {
    display: block;
    max-width: 60px;
}

.user_container button{
    width: 70px;
}

.paginationContainer ul li .active {
    font-weight: bold !important;
    border: 1px solid gray;
}

.paginationContainer ul{
    display: flex;
    list-style: none;
}

